@import '../../styles';

.itemCard {
  //width: 265px;
  border-radius: 10px;
  object-fit: cover;
  overflow: hidden;
  background: var(--bgc-main);

  &Thumb {
    padding: 5px 10px 10px 10px;
    @include text;
  }

  &Article {
    font-size: 0.9rem;
    line-height: 1;
    letter-spacing: 0.05px;
    color: var(--text-secondary);
    margin-bottom: 10px;
    font-style: italic;
  }

  &Title {
    font-size: 1.2rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 15px;
  }

  &Size {
    margin-bottom: 10px;
    font-size: 0.9rem;
    font-style: italic;
  }

  &Price {
    font-weight: 600;
  }
}
