@import '../../styles';

.shoes {
  &Container {
    @include containerDesktop;
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &Title {
    @include title;
    text-align: center;
    margin-bottom: 0.5rem;
  }

  &BtnList {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px;
  }

  &CardList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 15px;
    row-gap: 25px;
  }
}
