@import '../../styles';

.header {
  //position: fixed;
  width: 100%;
  //top: 0;
  //left: 0;
  //z-index: 5;
  border-bottom: 1px solid var(--text-gray);
  background-color: var(--bgc-main);

  &Container {
    @include containerDesktop;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0.8rem;
    padding-bottom: 0.8rem;
  }

  &Logo {
    width: 195px;
    height: 55px;
    order: 1;
  }

  &Nav {
    order: 2;
  }
}

.basket {
  position: relative;
  order: 3;

  &Img {
    //width: 24px; //mobile
    width: 30px;
    //height: 24px; //mobile
    height: 30px;
  }

  &Label {
    position: absolute;
    top: -3px;
    right: -3px;
    font-size: 0.8rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 16px;
    height: 16px;
    background-color: red;
    border-radius: 50%;
  }
}

// Tablet
@include tablet {
  .container {
    @include containerTablet;
  }
  .header {
    &Logo {
      width: 170px;
      height: 45px;
      order: 1;
    }

    &Nav {
      order: 3;
      margin-left: 20px;
    }
  }

  .basket {
    margin-left: auto;
    position: relative;
    order: 2;
  }
}

// Mobile
@include mobile {
  .container {
    @include containerMobile;
  }

  .header {
    &Logo {
      width: 145px;
      height: 40px;
    }
  }

  .basket {

    &Img {
      width: 26px;
      height: 26px;
    }
  }
}


